.fontWrapperCard {
  min-width: 60px;
}
.cardLi {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.cardLi .icon {
  border: 1px solid grey;
  padding: 3px;
  border-radius: 5px;
  margin: 1px 2px;
  cursor: pointer;
}
.cardLi .icon:nth-child(2) {
  color: var(--white);
  background-color: var(--black);
}
.cardLi p {
  text-align: left;
  flex-grow: 1;
  margin-left: 5px;
  overflow: hidden;
  white-space: nowrap;
}
.cartListAmountWrapper {
  min-width: 130px;
  margin-left: 10px;
}
.itemMax {
  position: absolute;
  color: red;
  font-size: 0.8rem;
  left: 15px;
}
.iconNotavailable {
  background-color: var(--greyHover);
  border: 1px solid grey;
  padding: 3px;
  border-radius: 5px;
  margin: 1px 2px;
  cursor: pointer;
}
@media (max-width: 600px) {
  .cartListAmountWrapper {
    min-width: none;
    margin-left: 10px;
  }
  .fontWrapperCard {
    display: none;
  }
  .itemMax {
    display: none;
  }
}
